header {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3a8035;
}

header img {
    width: 100px;
}

header nav {
    width: 50%;
    display: flex;
    justify-content: space-around;
}

header nav a {
    text-decoration: none;
    color: white;
    font-family: Arial;
}